import React from 'react';
import { Dropdown } from './dropdown';

function FormClients(props) {
  const {
    edit,
    clearStates,
    name,
    setName,
    email,
    setEmail,
    InputMask,
    phone,
    setPhone,
    segments,
    setSegmentSelect,
    segmentSelect,
    updateClient,
    newClient,
    addUser,
    Alert,
    invalidClient,
    alreadyExist,
  } = props;

  return (
    <div
      className="modal fade"
      id="modalClient"
      tabIndex="-1"
      role="dialog"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-dialog-centered" role="document">
        <div className="modal-content">
          <div className="modal-header">
            {edit ? (
              <h5 className="modal-title">Editar Cliente</h5>
            ) : (
              <h5 className="modal-title">Novo Cliente</h5>
            )}
            <button
              type="button"
              className="close"
              onClick={clearStates}
              data-dismiss="modal"
              aria-label="Fechar"
              key={0}
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div className="modal-body">
            <form>
              <label htmlFor="name">Cliente</label>
              <input
                type="text"
                className="form-control mb-3"
                id="client"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder={'Digite o nome do cliente'}
                required={true}
              />
              <label htmlFor="name">E-mail</label>
              <input
                type="text"
                className="form-control mb-2"
                id="procedure"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder={'Digite o email'}
                required={false}
              />
              <label htmlFor="name">Telefone</label>
              <InputMask
                type="text"
                format="currency"
                className="form-control mb-2"
                value={phone}
                id="contact"
                mask="(99) 9 9999-9999"
                onChange={(e) => setPhone(e.target.value)}
                placeholder="(__) _ ____ - ____"
                required={true}
              />
              {segments.length > 0 && (
                <div>
                  <label htmlFor="name" className="mt-2">
                    Segmento
                  </label>
                  <Dropdown
                    title="Selecionar um segmento"
                    options={segments.map((item) => ({
                      name: item.segment,
                      id: item.id,
                    }))}
                    selectValue={(e, item) => setSegmentSelect(item)}
                  />
                </div>
              )}
              {segmentSelect && (
                <div className="text-primary font-weight-bold ml-1 mt-3 mb-2">
                  Selecionado:{' '}
                  <small className="text-muted h6 mb-3">
                    {segmentSelect.name || 'Nenhum'}
                  </small>
                </div>
              )}
              <div className="modal-footer mt-4">
                <button
                  type="reset"
                  onClick={clearStates}
                  className="btn btn-outline-secondary col"
                  data-dismiss="modal"
                  key={3}
                >
                  Fechar
                </button>
                {edit ? (
                  <button
                    type="button"
                    onClick={updateClient}
                    className="btn btn-primary col"
                    key={4}
                  >
                    Salvar
                  </button>
                ) : (
                  <button
                    type="button"
                    onClick={newClient}
                    className="btn btn-primary col"
                    key={5}
                  >
                    Adicionar
                  </button>
                )}
              </div>
              {addUser === true ? (
                <Alert variant="success">Cliente adicionado com sucesso.</Alert>
              ) : null}
              {addUser === false ? (
                <Alert variant="danger">Preencha os campos corretamente.</Alert>
              ) : null}
              {invalidClient === true ? (
                <Alert variant="danger">
                  Não foi possível processar a requisição.
                </Alert>
              ) : null}
              {alreadyExist === true ? (
                <Alert variant="danger">Esse cliente já existe.</Alert>
              ) : null}
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export { FormClients };
