import { WhatsAppService } from '../services/whatsapp-service';
import { segmentService } from '../services/segmentService';
import { authService } from '../services/authService';
import { clientService } from '../services/clientService';

import { CardClients } from '../components/cardClient';
import { FormClients } from '../components/formClients';
import { TopModal } from '../components/topModal';

import { Dropdown } from '../components/dropdown';

import { HTTP_RESPONSE } from '../utils/constants';

import { Alert } from 'react-bootstrap';
import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import InputMask from 'react-input-mask';
import { ClearFields } from '../components/buttons';
import { CardSegment } from '../components/cardSegment';

function Segments() {
  const [redirectTo, setRedirectTo] = useState(null);
  const [id_user, setId_user] = useState(null);
  const [segments, setSegments] = useState([]);
  const [clients, setClients] = useState([]);
  const [id, setId] = useState(null);
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [edit, setEdit] = useState('');
  const [emptyClients, setEmptyClients] = useState(false);
  const [serverError, setServerError] = useState(false);
  const [segmentSelect, setSegmentSelect] = useState(null);
  const [addUser, setAddUser] = useState(null);
  const [invalidClient, setInvalidClient] = useState(null);

  const [segmentActual, setSegmentActual] = useState(null);
  const [clearClients, setClearClients] = useState(null);

  const [titleCardSegment, setTitleCardSegment] = useState('');
  const [actionName, setActionName] = useState('');
  const [editSegmentName, setEditSegmentName] = useState({ name: '', id: '' });

  const [createSegment, setCreateSegment] = useState(null);
  const [segmentAlreadyExist, setSegmentAlreadyExist] = useState(null);
  const [editSegment, setEditSegment] = useState(null);

  const [deleteSegmentSucess, setDeleteSegmentSucess] = useState(null);
  const [fieldsEmpty, setFieldsEmpty] = useState(null);
  const [existClientInSegment, setExistClientInSegment] = useState(null);

  const [deletedClient, setDeletedClient] = useState(null);

  useEffect(() => {
    Promise.all([
      authService.redirectLogin().then((result) => setRedirectTo(result)),
      authService.getUserIdInLocalStorange().then((result) => {
        setId_user(result);
        getSegments(result);
      }),
    ]);
  }, [id_user]);

  async function getSegments(result) {
    const request = await segmentService.getSegment(result);

    if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
      setServerError(true);
      return;
    }

    if (request.status === HTTP_RESPONSE.NOT_FOUND) {
      setSegments([]);
      return;
    }

    setSegments(request.data);
  }

  const getClientBySegment = async (event, params) => {
    event.preventDefault();
    setSegmentActual(params);

    const request = await clientService.findAllClientBySegment(
      id_user,
      params.name,
    );

    if (request.status === HTTP_RESPONSE.ERROR) {
      setServerError(true);
      return;
    }

    if (request.status === HTTP_RESPONSE.NOT_FOUND) {
      setEmptyClients(true);
      setClients([]);
      return;
    }

    setClients(request.data);
  };

  const activeEditClient = async (event, id, name, email, phone, segment) => {
    event.preventDefault();
    setEdit(true);
    setId(id);
    setName(name);
    setEmail(email);
    setPhone(phone);
    setSegmentSelect({ name: segment });
  };

  const clearStates = () => {
    setEdit(false);
    setName('');
    setEmail('');
    setPhone('');
    setSegmentSelect(null);
    setEditSegment(null);
  };

  const updateClient = async (event) => {
    event.preventDefault();

    const segmentSelected = segmentSelect ? segmentSelect.name : null;
    const emailField = email ? email.trim() : null;

    const request = await clientService.updateClient(
      id_user,
      id,
      name.trim(),
      emailField,
      phone.trim(),
      segmentSelected,
    );

    if (HTTP_RESPONSE.SUCCESS.includes(request.status)) {
      setAddUser(true);
      clearStates();
      setEdit(false);
      await getClientBySegment(event, segmentActual);
      return;
    }

    if (request.status === HTTP_RESPONSE.NOT_FOUND) {
      setAddUser(false);
      return;
    }

    if (request.status === HTTP_RESPONSE.ERROR) {
      setServerError(true);
      return;
    }
  };

  const deleteClient = async (event, id) => {
    event.preventDefault();
    const request = await clientService.deleteClient(id_user, id);

    if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
      setServerError(true);
      return;
    }

    clearStates();
    setDeletedClient(true);
    await getClientBySegment(event, segmentActual);
    return;
  };

  const clearFindClients = (event) => {
    event.preventDefault();

    setClients([]);
    setClearClients(true);
    setSegmentActual(null);
  };

  const createSegmentRequest = async (event, segment) => {
    event.preventDefault();

    if (!segment || !segment.trim()) {
      setFieldsEmpty(true);
      return;
    }

    const request = await segmentService.createSegment(id_user, segment);

    if (request.status === HTTP_RESPONSE.CONFLICT) {
      setSegmentAlreadyExist(true);
      return;
    }

    if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
      setServerError(true);
      return;
    }

    setEditSegmentName('');
    setCreateSegment(true);
    await getSegments(id_user);
  };

  const updateSegmentRequest = async (event, segment, id) => {
    event.preventDefault();

    const request = await segmentService.updateSegment(id_user, id, segment);

    if (request.status === HTTP_RESPONSE.CONFLICT) {
      setSegmentAlreadyExist(true);
      return;
    }

    if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
      setServerError(true);
      return;
    }

    setEditSegmentName('');
    setCreateSegment(true);
    await getSegments(id_user);
  };

  const deleteSegment = async (event, id, segment) => {
    event.preventDefault();

    const request = await segmentService.deleteSegment(id_user, id, segment);

    if (request.status === HTTP_RESPONSE.ERROR) {
      setServerError(true);
      return;
    }

    if (request.status === HTTP_RESPONSE.CONFLICT) {
      setExistClientInSegment(true);
      return;
    }

    setDeleteSegmentSucess(true);
    setSegmentActual('');
    await getSegments(id_user);
  };

  if (redirectTo) {
    return <Navigate to={redirectTo} />;
  }

  if (addUser === false || addUser === true) {
    setTimeout(() => setAddUser(null), 5000);
  }

  if (invalidClient === false || invalidClient === true) {
    setTimeout(() => setInvalidClient(null), 5000);
  }

  if (emptyClients === true) {
    setTimeout(() => setEmptyClients(false), 5000);
  }

  if (serverError === true) {
    setTimeout(() => setServerError(false), 5000);
  }

  if (clearClients === true) {
    setTimeout(() => setClearClients(false), 5000);
  }

  if (createSegment === true) {
    setTimeout(() => setCreateSegment(false), 5000);
  }

  if (segmentAlreadyExist === true) {
    setTimeout(() => setSegmentAlreadyExist(false), 5000);
  }

  if (deleteSegmentSucess === true) {
    setTimeout(() => setDeleteSegmentSucess(false), 5000);
  }

  if (fieldsEmpty === true) {
    setTimeout(() => setFieldsEmpty(false), 5000);
  }

  if (existClientInSegment === true) {
    setTimeout(() => setExistClientInSegment(false), 8000);
  }

  if (deletedClient === true) {
    setTimeout(() => setDeletedClient(false), 5000);
  }

  return (
    <div className="container-main">
      <h3 className="title-page">Segmentos</h3>

      <TopModal
        className="btn btn-danger"
        id="delete-client"
        title="Excluir cliente?"
        body="Tem certeza que deseja excluir esse cliente?"
        click={(e) => deleteClient(e, id)}
        button="Excluir"
      />

      <TopModal
        className="btn btn-danger"
        id="delete-segment"
        title="Excluir segmento?"
        body="Tem certeza que deseja excluir esse segmento?"
        click={(e) => deleteSegment(e, segmentActual.id, segmentActual.name)}
        button="Excluir"
      />

      <CardSegment
        editSegment={editSegment}
        title={titleCardSegment}
        actionName={actionName}
        segment={editSegmentName}
        clearStates={(e) => setEditSegmentName({ name: '', id: '' })}
        actionCreate={(e, segment) => createSegmentRequest(e, segment)}
        actionUpdate={updateSegmentRequest}
        setNewSegment={setEditSegmentName}
        createSegment={createSegment}
        segmentAlreadyExist={segmentAlreadyExist}
        serverError={serverError}
        fieldsEmpty={fieldsEmpty}
      />

      <button
        className="btn btn-outline-primary mb-4 p-2 col"
        data-toggle="modal"
        data-target="#modalSegment"
        onClick={(e) => {
          setTitleCardSegment('Novo');
          setActionName('Criar');
          setEditSegment(false);
        }}
      >
        Novo Segmento
      </button>

      <div className="card" style={{ borderRadius: '10px' }}>
        <div className="d-flex align-items-baseline pt-3 pl-3 pb-3">
          <h6 className="text-primary font-weight-bold">Busca:</h6>
          <div className="col">
            {segments.length > 0 ? (
              <Dropdown
                title="Selecionar"
                options={segments.map((item) => ({
                  name: item.segment,
                  id: item.id,
                }))}
                selectValue={(e, item) => getClientBySegment(e, item)}
                className=""
                style={{ borderRadius: '20px' }}
                keyProp={Math.random()}
              />
            ) : (
              <h6 className="p-3">Nenhum segmento encontrado</h6>
            )}
          </div>
        </div>
      </div>

      <h5 className="mt-4">
        Selecionado:
        <small className="text-primary font-weight-bold">
          {segmentActual ? ` ${segmentActual.name}` : ' Nenhum'}
        </small>
      </h5>

      {segmentActual && segmentActual.name && segmentActual.name.length > 0 ? (
        <div className="d-flex flex-wrap flew-row justify-content-between mt-3 border mb-3"
          style={{ borderRadius: '15px', padding: '10px', backgroundColor: 'white' }}
        >
          <button
            className="col mt-2 mb-2 mr-1 btn btn-outline-primary font-weight-bold"
            data-toggle="modal"
            data-target="#modalSegment"
            onClick={(e) => {
              setTitleCardSegment('Editar');
              setActionName('Salvar');
              setEditSegmentName({
                name: segmentActual.name,
                id: segmentActual.id,
              });
              setEditSegment(true);
            }}
          >
            Editar Segmento
          </button>
          <button
            className="col mt-2 mb-2 ml-1 btn btn-outline-danger font-weight-bold"
            data-toggle="modal"
            data-target="#delete-segment"
            onClick={(e) => e}
          >
            Deletar Segmento
          </button>
          <ClearFields
            title="Limpar Pesquisa"
            callback={clearFindClients}
            className="col-12 font-weight-bold"
          />
        </div>
      ) : null}

      {serverError === true && (
        <Alert variant="danger" className="mt-3">
          Não foi possível processar a requisição.
        </Alert>
      )}
      {clearClients === true && (
        <Alert variant="success" className="mt-3">
          A pesquisa foi limpa.
        </Alert>
      )}
      {deleteSegmentSucess === true && (
        <Alert variant="success" className="mt-3">
          Segmento deletado com sucesso.
        </Alert>
      )}
      {existClientInSegment === true && (
        <Alert variant="danger" className="mt-3">
          Esse segmento não pode ser excluído, pois existem clientes associados à ele.
        </Alert>
      )}
      {deletedClient === true && (
        <Alert variant="success" className="mt-3">
          Cliente deletado com sucesso.
        </Alert>
      )}

      <div>
        {clients.length > 0 && (
          <div className="pt-2">
            <div className="d-inline">
              <strong>Clientes nesse segmento: </strong>
              <div className="d-inline mb-3">{clients.length}</div>
            </div>

            <CardClients
              clients={clients}
              redirectToWhatsapp={WhatsAppService.redirectToWhatsapp}
              activeEditClient={activeEditClient}
              setId={setId}
            />
          </div>
        )}
        {emptyClients === true && (
          <Alert variant="danger" className="mt-3">
            Nenhum cliente nesse segmento.
          </Alert>
        )}
      </div>
      <FormClients
        edit={edit}
        clearStates={clearStates}
        name={name}
        setName={setName}
        email={email}
        setEmail={setEmail}
        InputMask={InputMask}
        phone={phone}
        setPhone={setPhone}
        segments={segments}
        setSegmentSelect={setSegmentSelect}
        segmentSelect={segmentSelect}
        updateClient={updateClient}
        addUser={addUser}
        Alert={Alert}
      />
    </div>
  );
}

export { Segments };
