import '../css/main.css';
import { authService } from '../services/authService';
import { salesService } from '../services/salesService';

import { TableSales } from '../components/tableSales';
import { ClearFields } from '../components/buttons';

import { HTTP_RESPONSE } from '../utils/constants';

import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import { Alert } from 'react-bootstrap';

export default function Sales() {
  const [redirectTo, setRedirectTo] = useState(null);
  const [date1, setDate1] = useState('');
  const [date2, setDate2] = useState('');
  const [sales, setSales] = useState([]);
  const [errorSales, setErrorSales] = useState(null);
  const [serverError, setServerError] = useState(null);
  const [salesTodayNotFound, setSalesTodayNotFound] = useState(null);
  const [clearFields, setClearFields] = useState(null);

  const [user_id, setUser_id] = useState('');

  useEffect(() => {
    Promise.all([
      authService.redirectLogin().then((result) => setRedirectTo(result)),
      authService
        .getUserIdInLocalStorange()
        .then((result) => setUser_id(result)),
      getSalesTodayResponse(),
    ]);

    async function getSalesTodayResponse() {
      const userId = await authService.getUserIdInLocalStorange();
      const request = await salesService.getSalesToday(userId);

      setDate1(new Date());

      if (HTTP_RESPONSE.SUCCESS.includes(request.status)) {
        setSales(request.data);
      }

      if (request.status === HTTP_RESPONSE.NOT_FOUND) {
        setSalesTodayNotFound(true);
      }

      if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
        setServerError(true);
      }
    }
  }, [user_id]);

  const getSalesInPeriodResponse = async (event) => {
    event.preventDefault();
    if (date1 === '' || !date1) {
      setErrorSales(true);
    }
    const request = await salesService.getSalesInPeriod(user_id, date1, date2);

    if (request.status === HTTP_RESPONSE.NOT_FOUND) {
      setErrorSales(true);
      setSales([]);
      return;
    }

    if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
      setServerError(true);
    }

    setSales(request.data);
    setErrorSales(false);
  };

  const clearSales = (event) => {
    event.preventDefault();

    setSales([]);
    document.getElementById('date1').value = '';
    document.getElementById('date2').value = '';
    setDate1('');
    setDate2('');
    setClearFields(true);
  };

  if (errorSales === false || errorSales === true) {
    setTimeout(() => setErrorSales(null), 5000);
  }

  if (serverError === false || serverError === true) {
    setTimeout(() => setServerError(null), 5000);
  }

  if (salesTodayNotFound) {
    setTimeout(() => setSalesTodayNotFound(null), 5000);
  }

  if (clearFields) {
    setTimeout(() => setClearFields(null), 5000);
  }

  if (redirectTo) {
    return <Navigate to={redirectTo} />;
  }

  return (
    <div className="container-main">
      <h3 className="title-page">Suas vendas</h3>
      <div className="card mb-4 p-3">
        <h4 className="mb-3">Selecione o período</h4>
        <form onSubmit={(e) => getSalesInPeriodResponse(e)}>
          <div className="form-row d-flex align-items-center">
            <div className="col mb-3">
              <label>Primeira data</label>
              <input
                type="date"
                className="form-control"
                id="date1"
                onChange={(e) => setDate1(e.target.value)}
                required={true}
              />
              <small className="form-text text-muted">
                Selecione a primeira data.
              </small>
            </div>
            <div className="col mb-3">
              <label>Segunda data</label>
              <input
                type="date"
                className="form-control"
                id="date2"
                onChange={(e) => setDate2(e.target.value)}
                required={false}
              />
              <small className="form-text text-muted">
                Selecione a segunda data.
              </small>
            </div>
            <button type="submit" className="col-md-2 btn btn-primary mb-1">
              Selecionar
            </button>
          </div>
        </form>
      </div>

      {errorSales === true ? (
        <Alert variant="danger">Nenhuma venda encontrada nesse período.</Alert>
      ) : null}
      {errorSales === false ? (
        <Alert variant="success">Período atualizado.</Alert>
      ) : null}
      {serverError === true ? (
        <Alert variant="danger">Não foi possível processar a requisição.</Alert>
      ) : null}
      {salesTodayNotFound === true ? (
        <Alert variant="danger">Nenhuma venda encontrada hoje.</Alert>
      ) : null}
      {clearFields === true ? (
        <Alert variant="success">A pesquisa foi limpa.</Alert>
      ) : null}

      {sales.length > 0 ? (
        <div>
          <div className="d-inline">
            <strong>Período: </strong>
            {(date1 || date2) && (
              <div className="d-inline">
                {date1 &&
                  new Date(date1).toLocaleDateString('pt-BR', {
                    timeZone: 'UTC',
                  })}
                {date2 &&
                  ' - ' +
                    new Date(date2).toLocaleDateString('pt-BR', {
                      timeZone: 'UTC',
                    })}
              </div>
            )}
          </div>

          <ClearFields title="Limpar Pesquisa" callback={clearSales} />

          <TableSales sales={sales} />

          <div className="inline">
            <p>
              <strong>Quantidade de vendas:</strong>{' '}
              {sales.length > 0 ? sales.length : null}
            </p>
          </div>
          <div className="total-sale">
            <h4>Total:</h4>
            <h4>
              {sales.length > 0
                ? salesService.countTotalValueSales(sales)
                : null}
            </h4>
          </div>
        </div>
      ) : (
        <h4>Nenhuma venda encontrada.</h4>
      )}
    </div>
  );
}
