import '../css/main.css';
import { authService } from '../services/authService';
import { scheduleService } from '../services/scheduleService';
import { clientService } from '../services/clientService';

import { NewSchedule } from '../components/newSchedule';
import { TopModal } from '../components/topModal';
import { ScheduleCard } from '../components/scheduleCard';
import { ClearFields } from '../components/buttons';

import { HTTP_RESPONSE } from '../utils/constants';

import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import { Alert } from 'react-bootstrap';

export default function Schedule() {
  const [redirectTo, setRedirectTo] = useState(null);
  const [schedules, setSchedule] = useState([]);
  const [id, setId] = useState('');
  const [client, setClient] = useState('');
  const [pacote, setPacote] = useState('');
  const [qtdTotalAtendimento, setQtdTotalAtendimento] = useState('');
  const [procedure, setProcedure] = useState('');
  const [dateNewSchedule, setDateNewSchedule] = useState('');
  const [time, setTime] = useState('');
  const [price, setPrice] = useState('');
  const [contact, setContact] = useState('');
  const [date, setDate] = useState('');
  const [errorSchedule, setErrorSchedule] = useState(null);
  const [errorClientSchedule, setErrorClientSchedule] = useState(null);
  const [serverError, setServerError] = useState(null);
  const [finalitySchedule, setFinalitySchedule] = useState(null);
  const [edit, setEdit] = useState(false);
  const [deleted, setDeleted] = useState(null);
  const [user_id, setUser_id] = useState('');

  const [clientSaves, setClientSaves] = useState(null);
  const [expiredSchedules, setExpiredSchedules] = useState([]);

  const [clearSchedule, setClearSchedule] = useState(null);
  const [scheduleFinish, setScheduleFinish] = useState(null);

  useEffect(() => {
    Promise.all([
      authService.redirectLogin().then((result) => setRedirectTo(result)),
      authService.getUserIdInLocalStorange().then((result) => {
        setUser_id(result);
        scheduleService.getAllExpiredSchedules(result).then((result) => {
          if (!result.status) return;
          if (result.status === HTTP_RESPONSE.NOT_FOUND) return;
          setExpiredSchedules(result.data);
        });
      }),
      getClients(),
    ]);

    async function getClients() {
      const userId = await authService.getUserIdInLocalStorange();
      const clients = await clientService.findAllClient(userId);
      setClientSaves(clients.data);
    }
  }, []);

  if (redirectTo) {
    return <Navigate to={redirectTo} />;
  }

  const clearStates = () => {
    setEdit(false);
    setId('');
    setClient('');
    setProcedure('');
    setDateNewSchedule('');
    setTime('');
    setPrice('');
    setContact('');
    setPacote(null);
    setQtdTotalAtendimento(null);
  };

  const setDataClient = (event, params) => {
    event.preventDefault();
    setClient(params.name);
    setContact(params.phone);
  };

  const setQtdTotalAtendimentoFuncion = (event, qtd) => {
    event.preventDefault();
    setQtdTotalAtendimento(qtd);
  };

  const setPacoteFunc = (event, pacote) => {
    event.preventDefault();
    setPacote(pacote);
  };

  const getScheduleResponse = async (event) => {
    event.preventDefault();
    const request = await scheduleService.getClientsSchedule(user_id, date);

    if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
      setServerError(true);
      return;
    }

    if (request.status === HTTP_RESPONSE.NOT_FOUND) {
      setErrorSchedule(true);
      setSchedule([]);
      return;
    }

    setSchedule(request.data);
    setErrorSchedule(false);
  };

  const finishScheduleResponse = async (event, id) => {
    event.preventDefault();

    const scheduleFinish = schedules
      .map((item) => item)
      .filter((item) => item.id === id);

    const expiredScheduleFinish = expiredSchedules
      .map((item) => item)
      .filter((item) => item.id === id);

    scheduleFinish.push(...expiredScheduleFinish);

    const request = await scheduleService.finishClientSchedule(
      user_id,
      scheduleFinish[0].id,
    );

    if (!request.status || request.status === HTTP_RESPONSE.ERROR) {
      setServerError(true);
      return;
    }

    setScheduleFinish(true);
    await getScheduleResponse(event);
  };

  const addNewClientSchedule = async (event) => {
    event.preventDefault();
    const request = await scheduleService.addClientSchedule(
      user_id,
      client,
      procedure,
      dateNewSchedule,
      time,
      price,
      contact,
      pacote,
      qtdTotalAtendimento,
    );

    if (HTTP_RESPONSE.SUCCESS.includes(request.status)) {
      setErrorClientSchedule(false);
      clearStates();
    }

    if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
      setErrorClientSchedule(true);
    }
  };

  const activeEditSchedule = async (
    event,
    id,
    client,
    procedure,
    dateNewSchedule,
    time,
    price,
    contact,
    pacote,
    qtdTotalAtendimento,
  ) => {
    event.preventDefault();
    setId(id);
    setEdit(true);
    setClient(client);
    setProcedure(procedure);
    setDateNewSchedule(dateNewSchedule);
    setTime(time);
    setPrice(price);
    setContact(contact);
    setPacote(pacote);
    setQtdTotalAtendimento(qtdTotalAtendimento);
  };

  const updateClientScheduleResponse = async (event) => {
    event.preventDefault();

    const request = await scheduleService.updateClientSchedule(
      user_id,
      id,
      client,
      procedure,
      dateNewSchedule,
      time,
      price,
      contact,
      pacote,
      qtdTotalAtendimento,
    );

    if (HTTP_RESPONSE.SUCCESS.includes(request.status)) {
      setErrorClientSchedule(false);
      clearStates();
    }

    if (
      request.status === HTTP_RESPONSE.ERROR ||
      request.status === HTTP_RESPONSE.ERROR ||
      !request.status
    ) {
      setErrorClientSchedule(true);
    }

    getScheduleResponse(event);
  };

  const deleteClientScheduleResponse = async (event, id) => {
    event.preventDefault();
    const result = await scheduleService.deleteClientSchedule(user_id, id);

    if (result === HTTP_RESPONSE.SUCCESS) {
      setErrorClientSchedule(false);
      clearStates();
    }

    if (!result.status) {
      setErrorClientSchedule(true);
    }

    await getScheduleResponse(event);
  };

  const clearFindSchedule = (event) => {
    event.preventDefault();

    setSchedule([]);
    document.getElementById('dateLoadSchedule').value = '';
    setDate('');
    setClearSchedule(true);
  };

  if (errorSchedule === false || errorSchedule) {
    setTimeout(() => setErrorSchedule(null), 5000);
  }

  if (errorClientSchedule === false || errorClientSchedule) {
    setTimeout(() => setErrorClientSchedule(null), 5000);
  }

  if (serverError) {
    setTimeout(() => setServerError(null), 5000);
  }

  if (deleted === false) {
    setTimeout(() => setDeleted(null), 5000);
  }

  if (clearSchedule) {
    setTimeout(() => setClearSchedule(null), 5000);
  }

  if (scheduleFinish) {
    setTimeout(() => setScheduleFinish(null), 5000);
  }

  if (finalitySchedule) {
    setSchedule([]);
    setFinalitySchedule(null);
    getScheduleResponse();
  }

  return (
    <div className="container-main">
      <TopModal
        id="finality-client-schedule"
        title="Horário finalizado com sucesso!"
        body="Você finalizou esse horário e ele já foi salvo nas suas vendas!"
      />

      <TopModal
        className="btn btn-danger"
        id="delete-client-schedule"
        title="Excluir horário?"
        body="Tem certeza que deseja excluir esse horário?"
        click={(e) => deleteClientScheduleResponse(e, deleted)}
        button="Excluir"
      />

      <TopModal
        className="btn btn-primary"
        id="finalyt-schedule"
        title="Finalizar horário?"
        data_target="#finality-client-schedule"
        body="Tem certeza que deseja finalizar esse horário?"
        click={(e) => finishScheduleResponse(e, id)}
        button="Finalizar"
      />

      <h3 className="mb-3 title-page">Agenda</h3>

      <button
        type="button"
        className="btn btn-outline-primary col mt-3 p-2"
        data-toggle="modal"
        data-target="#crudScheduleModal"
      >
        Adicionar Nova Agenda
      </button>

      <div
        className="modal fade"
        id="crudScheduleModal"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="titleModalSchedule"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content">
            <div className="modal-header">
              {edit ? (
                <h5 className="modal-title" id="titleModalSchedule">
                  Editar Agenda
                </h5>
              ) : (
                <h5 className="modal-title" id="titleModalSchedule">
                  Adicionar Nova Agenda
                </h5>
              )}
              <button
                type="button"
                className="close"
                onClick={clearStates}
                data-dismiss="modal"
                aria-label="Fechar"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>

            <NewSchedule
              clientSaves={clientSaves}
              setDataClient={(e, item) => setDataClient(e, item)}
              client={client}
              setClient={setClient}
              procedure={procedure}
              setProcedure={setProcedure}
              dateNewSchedule={dateNewSchedule}
              setDateNewSchedule={setDateNewSchedule}
              time={time}
              setTime={setTime}
              price={price}
              setPrice={setPrice}
              contact={contact}
              setContact={setContact}
              clearStates={(e) => clearStates()}
              edit={edit}
              updateClientScheduleResponse={(e) =>
                updateClientScheduleResponse(e)
              }
              addNewClientSchedule={(e) => addNewClientSchedule(e)}
              errorClientSchedule={errorClientSchedule}
              setPacoteFunc={setPacoteFunc}
              pacote={pacote}
              setQtdTotalAtendimentoFuncion={setQtdTotalAtendimentoFuncion}
              qtdTotalAtendimento={qtdTotalAtendimento}
            />
          </div>
        </div>
      </div>

      <div className="card p-3 mt-4 mb-4">
        <h4>Selecione a Data</h4>
        <form onSubmit={getScheduleResponse}>
          <div className="form-row">
            <div className="col-7 mb-3">
              <input
                type="date"
                className="form-control"
                id="dateLoadSchedule"
                value={date}
                onChange={(e) => setDate(e.target.value)}
                required={true}
              />
              <small className="form-text text-muted">
                Selecione a data desejada.
              </small>
            </div>
            <div className="col mb-3">
              <button type="submit" className="col btn btn-primary">
                Selecionar
              </button>
            </div>
          </div>
        </form>
      </div>

      {errorSchedule === true ? (
        <Alert variant="danger">Nenhum horário agendado nessa data.</Alert>
      ) : null}
      {errorSchedule === false ? (
        <Alert variant="success">Pesquisa atualizada.</Alert>
      ) : null}
      {serverError ? (
        <Alert variant="danger">Nenhum horário foi retornado.</Alert>
      ) : null}
      {deleted === false ? (
        <Alert variant="success">Horário excluído com sucesso.</Alert>
      ) : null}
      {clearSchedule === true ? (
        <Alert variant="success">A pesquisa foi limpa.</Alert>
      ) : null}
      {scheduleFinish === true ? (
        <Alert variant="success">Horário finalizado com sucesso.</Alert>
      ) : null}

      <h4 className="mb-3">Horários agendados</h4>

      {expiredSchedules ? (
        <div>
          {expiredSchedules.map((item) => {
            return (
              <ScheduleCard
                item={item}
                activeEditSchedule={activeEditSchedule}
                setId={setId}
                setDeleted={setDeleted}
                expired={true}
              />
            );
          })}
        </div>
      ) : null}

      {schedules.length > 0 ? (
        <div>
          <ClearFields title="Limpar Pesquisa" callback={clearFindSchedule} />

          {schedules.map((item) => {
            return (
              <ScheduleCard
                item={item}
                activeEditSchedule={activeEditSchedule}
                setId={setId}
                setDeleted={setDeleted}
                expired={false}
              />
            );
          })}
        </div>
      ) : null}
    </div>
  );
}
