import { mask } from '../services/maskMoney';
import { Dropdown } from './dropdown';

import { Alert } from 'react-bootstrap';
import InputMask from 'react-input-mask';

function NewSchedule(props) {
  const {
    clientSaves,
    setDataClient,
    client,
    setClient,
    procedure,
    setProcedure,
    dateNewSchedule,
    setDateNewSchedule,
    time,
    setTime,
    price,
    setPrice,
    contact,
    setContact,
    clearStates,
    edit,
    updateClientScheduleResponse,
    addNewClientSchedule,
    errorClientSchedule,
    setPacoteFunc,
    pacote,
    setQtdTotalAtendimentoFuncion,
    qtdTotalAtendimento,
  } = props;

  return (
    <div>
      <div className="modal-body">
        <form>
          <label htmlFor="name">Cliente</label>
          {clientSaves && clientSaves.length > 0 ? (
            <div>
              <Dropdown
                title="Selecionar Cliente"
                options={clientSaves}
                selectValue={(e, item) => setDataClient(e, item)}
              />
              <div className="text-center pt-2 pb-2">ou</div>
            </div>
          ) : null}
          <input
            type="text"
            className="form-control mb-3"
            id="client"
            value={client}
            onChange={(e) => setClient(e.target.value)}
            placeholder={'Digite o nome do cliente'}
            required={true}
          />
          <label htmlFor="name">Procedimento</label>
          <input
            type="text"
            className="form-control"
            id="procedure"
            value={procedure}
            onChange={(e) => setProcedure(e.target.value)}
            placeholder={'Digite o procedimento'}
            required={true}
          />
          <small className="form-text text-muted mb-3">
            Ex.: Corte de cabelo, massagem, sobrancelha, etc.
          </small>

          <div className="form-row">
            <div className="form-group col">
              <label htmlFor="name">Pacote</label>
              <div className="card">
                <button
                  className="btn bg-white text-left"
                  type="button"
                  id="dropdownMenuButton"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <div className="form-row">
                    <div className="col">
                      {pacote === true && 'Sim'}
                      {pacote === false && 'Não'}
                      {pacote === null || pacote === '' ? 'Escolha...' : null}
                    </div>
                    <div className="dropdown-toggle col-end" />
                  </div>
                </button>
                <div
                  className="dropdown-menu col"
                  aria-labelledby="dropdownMenuButton"
                  id="select-clients"
                >
                  <div className="dropdown-item">
                    <button
                      type="button"
                      onClick={(e) => setPacoteFunc(e, true)}
                      className="card p-2 mb-2 border-0 col"
                    >
                      Sim
                    </button>
                  </div>
                  <div className="dropdown-item">
                    <button
                      type="button"
                      onClick={(e) => setPacoteFunc(e, false)}
                      className="card p-2 mb-2 border-0 col"
                    >
                      Não
                    </button>
                  </div>
                </div>
              </div>
            </div>
            <div className="form-group col">
              <label htmlFor="name">Qtd Atendimento</label>
              <div className="card">
                <button
                  className="btn bg-white text-left"
                  type="button"
                  id="dropdownMenuButton"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <div className="form-row">
                    <div className="col">
                      {qtdTotalAtendimento ? qtdTotalAtendimento : 'Escolha...'}
                    </div>
                    <div className="dropdown-toggle col-end" />
                  </div>
                </button>
                <div
                  className="dropdown-menu col"
                  aria-labelledby="dropdownMenuButton"
                  id="select-clients"
                >
                  <div className="dropdown-item">
                    <div className="dropdown-item">
                      <button
                        type="button"
                        onClick={(e) => setQtdTotalAtendimentoFuncion(e, 1)}
                        className="card p-2 border-0 col"
                      >
                        1
                      </button>
                    </div>
                  </div>
                  <div className="dropdown-item">
                    <div className="dropdown-item">
                      <button
                        type="button"
                        onClick={(e) => setQtdTotalAtendimentoFuncion(e, 2)}
                        className="card p-2 border-0 col"
                      >
                        2
                      </button>
                    </div>
                  </div>
                  <div className="dropdown-item">
                    <div className="dropdown-item">
                      <button
                        type="button"
                        onClick={(e) => setQtdTotalAtendimentoFuncion(e, 3)}
                        className="card p-2 border-0 col"
                      >
                        3
                      </button>
                    </div>
                  </div>
                  <div className="dropdown-item">
                    <div className="dropdown-item">
                      <button
                        type="button"
                        onClick={(e) => setQtdTotalAtendimentoFuncion(e, 4)}
                        className="card p-2 border-0 col"
                      >
                        4
                      </button>
                    </div>
                  </div>
                  <div className="dropdown-item">
                    <div className="dropdown-item">
                      <button
                        type="button"
                        onClick={(e) => setQtdTotalAtendimentoFuncion(e, 5)}
                        className="card p-2 border-0 col"
                      >
                        5
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="form-row">
            <div className="form-group col">
              <label htmlFor="name">Data</label>
              <input
                type="date"
                className="form-control"
                id="date"
                value={dateNewSchedule}
                onChange={(e) => setDateNewSchedule(e.target.value)}
                required={true}
              />
              <small className="form-text text-muted">Selecione a data.</small>
            </div>
            <div className="form-group col">
              <label htmlFor="name">Horário</label>
              <input
                type="time"
                className="form-control"
                id="time"
                value={time}
                onChange={(e) => setTime(e.target.value)}
                required={true}
              />
              <small className="form-text text-muted">Selecione horário.</small>
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col">
              <label htmlFor="name">Preço</label>
              <input
                type="text"
                format="currency"
                className="form-control mb-3"
                value={price}
                onChange={(ev) => {
                  let val = ev.target.value;
                  const { maskedValue } = mask(val, 2, ',', '.', false, 'R$');
                  setPrice(maskedValue);
                }}
                id="price"
                placeholder="Digite o preço"
                required={true}
              />
            </div>
            <div className="form-group col">
              <label htmlFor="name">Telefone</label>
              <InputMask
                type="text"
                format="currency"
                className="form-control"
                value={contact}
                id="contact"
                mask="(99) 9 9999-9999"
                onChange={(e) => setContact(e.target.value)}
                placeholder="(__) _ ____ - ____"
                required={true}
              />
              <small className="form-text text-muted mb-3">
                Telefone de contato do whatsapp.
              </small>
            </div>
          </div>
          <div className="modal-footer">
            <button
              type="reset"
              onClick={clearStates}
              className="btn btn-outline-secondary col"
              data-dismiss="modal"
            >
              Fechar
            </button>
            {edit ? (
              <button
                type="button"
                onClick={updateClientScheduleResponse}
                className="btn btn-primary col"
              >
                Salvar
              </button>
            ) : (
              <button
                type="button"
                onClick={addNewClientSchedule}
                className="btn btn-primary col"
              >
                Adicionar
              </button>
            )}
          </div>
        </form>
        {errorClientSchedule === true ? (
          <Alert variant="danger">Preencha os campos corretamente.</Alert>
        ) : null}
        {errorClientSchedule === false ? (
          <Alert variant="success">Horário adicionado com sucesso.</Alert>
        ) : null}
      </div>
    </div>
  );
}

export { NewSchedule };
